.pagination-wrapper{width:100%;margin:0 auto;text-align:center}.pagination-wrapper .pagination-content{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;padding:0}.pagination-wrapper .pagination-content .pagination-item{padding:0 8px;margin:0 5px;height:24px;line-height:24px;border-radius:4px;font-size:14px;color:#98999c;text-align:center;cursor:pointer}.pagination-wrapper .pagination-content .pagination-item-active{background-color:#256cff;color:#fff}.pagination-wrapper .pagination-content .pagination-item-disabled{cursor:not-allowed}.pagination-wrapper li{list-style:none;outline-style:none}.pagination-wrapper .total-counter{margin-right:10px;color:#98999c;font-size:14px}